
.output-main {
  overflow: auto;
  width: 50%;
  height: 100%;
  box-sizing: border-box;
  font-size: 18px;
  border: none;
  resize: none;
  color: #34495e;
  text-align: justify;
  box-shadow: 0 6px 7px -4px rgba(0, 0, 0, 0.2), 0 11px 15px 1px rgba(0, 0, 0, 0.14), 0 4px 20px 3px rgba(0, 0, 0, 0.12);
  font-family: monospace;
}



