
.toolbar-main {
  position: fixed;
  z-index: 999;
}
.toolbar-main .toolbar-item {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 10px 15px;
    cursor: pointer;
}
.toolbar-main .toolbar-item i {
      color: rgba(102, 128, 153, 0.45098);
}
.toolbar-main .toolbar-item i:hover {
        color: rgba(44, 62, 80, 0.65083);
}
.input-main .toolbar-main {
  margin-left: 30%;
}
.output-main .toolbar-main {
  margin-left: 25%;
}



