
.menu-main {
  width: 100%;
  min-height: 40px;
}
.menu-main .article-title {
    font-size: 18px;
    font-family: monospace;
    color: #000;
}
.menu-main .article-list-item {
    height: 60px;
    background-color: #fff;
    color: #000;
    width: 100%;
}
.menu-main .article-list-item button {
      line-height: 60px;
      min-width: 200px;
      width: 80%;
      cursor: pointer;
      height: 60px;
      padding: 0;
}
.menu-main .article-list-item .md-icon-button {
      right: -15%;
      top: 0;
      padding: 0;
}
.menu-main .current {
    border-left: 8px solid #189BFA;
    background-color: #ECF0F1;
}



